import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "app/store";
import { Student, ListParams, PaginationParams, ListReponsive } from "modules";
export interface StudentState {
  loading: boolean;
  list: Student[];
  filter: ListParams;
  pagination: PaginationParams;
}
const initialState: StudentState = {
  loading: false,
  list: [],
  filter: {
    _page: 1,
    _limit: 5,
  },
  pagination: {
    _page: 1,
    _limit: 15,
    _totalRows: 15,
  },
};
const studentSlice = createSlice({
  name: "student",
  initialState,
  reducers: {
    // cc PayloadAction<ListParams> l cc param truyn t commpot ln
    fetchStudentList(state, action: PayloadAction<ListParams>) {
      state.loading = true;
    },
    fetchStudentListSuccess(
      state,
      action: PayloadAction<ListReponsive<Student>>
    ) {
      state.list = action.payload.data;
      state.pagination = action.payload.pagination;
      state.loading = false;
    },
    fetchStudentListFailed(state) {
      state.loading = false;
    },

    setFilter(state, action: PayloadAction<ListParams>) {
      state.filter = action.payload;
    },
    setFilterWithDebounce(state, action: PayloadAction<ListParams>) {},
  },
});
// Actions
export const studentActions = studentSlice.actions;
// Selector

// state.student l import   store
export const selectStudentList = (state: RootState) => state.student.list;
export const selectStudenLoading = (state: RootState) => state.student.loading;
export const selectStudentFilter = (state: RootState) => state.student.filter;
export const selectStudentPagination = (state: RootState) =>
  state.student.pagination;
//reducer
const studentReducer = studentSlice.reducer;
export default studentReducer;
