import * as React from "react";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { useAppDispatch, useAppSelector } from "app/hooks";
import { dashboardActions } from "./dashboardSlice";
import {
  selectDashboardLoading,
  selectDashboardStatistics,
  selectHighestStudentList,
  selectLowestStudentList,
  selectRankingByCityList,
} from "./dashboardSlice";
import StatisticItem from "./component/StatisticItem";
import { stat } from "fs";
import StudentRankingList from "./component/StudentRankingList";

// export default function DashBoard() {
//   const dispatch = useDispatch;
//   useEffect(() => {
//     dispatch(dashboardActions.fetchData());
//   }, [dispatch]);
//   return <div>dashbord</div>;
// }

export default function DashBoard() {
  const dispatch = useAppDispatch();
  const loading = useAppSelector(selectDashboardLoading);
  const statistics = useAppSelector(selectDashboardStatistics);
  const highestStudentList = useAppSelector(selectHighestStudentList);
  const lowestStudentList = useAppSelector(selectLowestStudentList);
  const rankingByCityList = useAppSelector(selectRankingByCityList);
  useEffect(() => {
    dispatch(dashboardActions.fetchData());
  }, [dispatch]);
  return (
    <div className="dashbord">
      <div className="statistic">
        <div className="row">
          <div className="col-md-3 stretch-card grid-margin">
            <StatisticItem label="Nam" value={statistics.maleCount} />
          </div>
          <div className="col-md-3 stretch-card grid-margin">
            <StatisticItem label="N" value={statistics.femaleCount} />
          </div>
          <div className="col-md-3 stretch-card grid-margin">
            <StatisticItem label="im >=8" value={statistics.highMarkCount} />
          </div>
          <div className="col-md-3  stretch-card grid-margin">
            <StatisticItem label="im <=5" value={statistics.lowMarkCount} />
          </div>
        </div>
      </div>
      <div className="all-student mt-3">
        <div className="row">
          <div className="col-md-12">
            <div className="title text-left mb-3">Tt c</div>
          </div>
          <div className="col-md-4  stretch-card grid-margin">
            <StudentRankingList
              title="Sinh vin c im cao nht"
              studentList={highestStudentList}
            />
          </div>
          <div className="col-md-4  stretch-card grid-margin">
            <StudentRankingList
              title="Sinh vin c im thp nht"
              studentList={lowestStudentList}
            />
          </div>
        </div>
      </div>
      <div className="ranking mt-3">
        <div className="row">
          <div className="col-md-12">
            <div className="title text-left mb-3">Xp hng theo thnh ph</div>
          </div>
          {rankingByCityList.map((ranking) => (
            <div
              className="col-md-3  stretch-card grid-margin"
              key={ranking.cityId}
            >
              <StudentRankingList
                title={ranking.cityName}
                studentList={ranking.rankingList}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
